class User {
    constructor() {
        console.log(1);
        this.registerEvents();
    }

    getUserList(id, pw) {
        if (!id) {
            const dataURL = "./data/data.json";
            const oReq = new XMLHttpRequest();
            oReq.addEventListener("load", () => {
                const list = JSON.parse(oReq.responseText).body;
                console.log(list);
            });
            oReq.open("GET", dataURL);
            oReq.send();
        } else {
        }
    }

    registerEvents() {
        //로그인
        const btn_login = document.querySelector("#btn_login");
        console.log(btn_login);
        btn_login.addEventListener((e) => {
            console.log(e);
        });
    }
}

export default User;
